module.exports = "<template><u-article><h1>FCollapseTransition 折叠过渡</h1>
<p>如果要给某些节点添加展开/收起动画效果，但不知道节点的高度，这时动画光靠 CSS 的<code>transition</code>属性是实现不了的，需要用 JS 进行计算。</p>
<p>本组件对这种动画进行了封装，方便快速实现这种效果。需要完成以下几步工作：</p>
<ul>
<li>给需要添加动画的节点外面包裹<code>&lt;f-collapse-transition&gt;</code>组件；</li>
<li>给需要添加动画的节点在 CSS 中添加针对<code>height</code>、<code>padding-top</code>和<code>padding-bottom</code>几个属性的<code>transition</code>动画；</li>
<li>使用<code>v-show</code>或<code>v-if</code>控制节点的显示/隐藏。</li>
</ul>
<h2>示例</h2>
<h3>基本形式</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="proto-ui.vusion/src/components/f-collapse-transition.vue/README.md">
