    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">:class</span>=<span class="hljs-string">"$style.root"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"toggle"</span>&gt;</span>展开/收起<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">f-collapse-transition</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">u-menu</span> <span class="hljs-attr">v-show</span>=<span class="hljs-string">"expanded"</span> <span class="hljs-attr">:class</span>=<span class="hljs-string">"$style.menu"</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">:router</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width: 128px;"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">u-menu-item</span> <span class="hljs-attr">to</span>=<span class="hljs-string">"/guides/"</span>&gt;</span>指南<span class="hljs-tag">&lt;/<span class="hljs-name">u-menu-item</span>&gt;</span>
