            <span class="hljs-tag">&lt;<span class="hljs-name">u-menu-item</span> <span class="hljs-attr">to</span>=<span class="hljs-string">"/components/"</span>&gt;</span>原型组件<span class="hljs-tag">&lt;/<span class="hljs-name">u-menu-item</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">u-menu-item</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"https://github.com/vusion"</span>&gt;</span>GitHub<span class="hljs-tag">&lt;/<span class="hljs-name">u-menu-item</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">u-menu</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">f-collapse-transition</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
