<h2>API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>axis</td>
<td>String</td>
<td><code>both</code></td>
<td>拖拽时移动路径的约束轴向。可选值：<code>both</code>表示可以在任意方向上移动、<code>horizontal</code>表示限制在水平方向上移动、<code>vertical</code>表示限制在垂直方向上移动。</td>
</tr>
<tr>
<td>grid</td>
<td>Object</td>
<td><code>{ x: 0, y: 0 }</code></td>
<td>f拖拽时移动路径的约束网格。值为一个{x,y}格式的对象，表示水平方向和垂直方向网格的大小。</td>
</tr>
<tr>
<td>range</td>
<td>String, Object</td>
<td></td>
<td>拖拽范围。值可以为一个{left,top,right,bottom}格式的对象，表示代理元素移动的上下左右边界。当值为<code>offset-parent</code>，代理元素限制在offsetParent中移动，仅适用于<code>position</code>为<code>absolute</code>的情况。</td>
</tr>
<tr>
<td>range-mode</td>
<td>String</td>
<td><code>inside</code></td>
<td>拖拽范围模式。可选值：<code>inside</code>表示在拖拽范围内侧移动，<code>center</code>表示在拖拽范围边缘及内侧移动，<code>outside</code>表示在拖拽范围外侧及内侧移动。</td>
</tr>
<tr>
<td>value</td>
<td>Any</td>
<td></td>
<td>需要传递的值</td>
</tr>
<tr>
<td>transfer</td>
<td>String, Element</td>
<td><code>'self'</code></td>
<td>拖拽时的移动元素。可选值：<code>'clone'</code>表示拖拽时拖起自身的一个拷贝，<code>'self'</code>表示拖拽时拖起自身，也可以直接传入一个元素，或者用slot来设置。</td>
</tr>
<tr>
<td>immediate</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否在鼠标按下时立即拖拽。默认为<code>false</code>，是为了防止鼠标单击等事件频繁触发拖拽操作。</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
</tbody>
</table>
