<td>Number</td>
<td>鼠标指针相对于屏幕的垂直坐标</td>
</tr>
<tr>
<td>$event.clientX</td>
<td>Number</td>
<td>鼠标指针相对于浏览器的水平坐标</td>
</tr>
<tr>
<td>$event.clientY</td>
<td>Number</td>
<td>鼠标指针相对于浏览器的垂直坐标</td>
</tr>
<tr>
<td>$event.pageX</td>
<td>Number</td>
<td>鼠标指针相对于页面的水平坐标</td>
</tr>
<tr>
<td>$event.pageY</td>
<td>Number</td>
<td>鼠标指针相对于页面的垂直坐标</td>
</tr>
<tr>
<td>$event.startX</td>
<td>Number</td>
