</tr>
<tr>
<td>$event.startY</td>
<td>Number</td>
<td>拖拽开始时鼠标指针的垂直坐标</td>
</tr>
<tr>
<td>$event.dragX</td>
<td>Number</td>
<td>拖拽时鼠标指针相对于起始坐标的水平位移</td>
</tr>
<tr>
<td>$event.dragY</td>
<td>Number</td>
<td>拖拽时鼠标指针相对于起始坐标的垂直位移</td>
</tr>
<tr>
<td>$event.startLeft</td>
<td>Number</td>
