</tr>
<tr>
<td>$event.startTop</td>
<td>Number</td>
<td>拖拽开始时代理元素的top值</td>
</tr>
<tr>
<td>$event.left</td>
<td>Number</td>
<td>拖拽时代理元素的left值</td>
</tr>
<tr>
<td>$event.top</td>
<td>Number</td>
<td>拖拽时代理元素的top值</td>
</tr>
<tr>
<td>$event.preventDefault</td>
<td>Function</td>
<td>阻止拖拽流程</td>
</tr>
<tr>
