module.exports = "<template><u-article><h1>MEmitter 发送器</h1>
<p>该 Mixin 集成了一些发送事件相关的方法。继承之后直接使用方法即可。</p>
<h2>示例</h2>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="proto-ui.vusion/src/components/m-emitter.vue/README.md">
    <div><anondemo-35aef168-476 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>这个例子中，该组件给上级添加了蓝色阴影。<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { MEmitter } <span class="hljs-keyword">from</span> <span class="hljs-string">'@@'</span>;

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">mixins</span>: [MEmitter],
    mounted() {
        <span class="hljs-keyword">this</span>.$contact(<span class="hljs-string">'u-code-example'</span>, (codeExampleVM) =&gt; {
            codeExampleVM.$el.style.boxShadow = <span class="hljs-string">'0 2px 10px rgb(69, 124, 208)'</span>;
        });
    },
    destroyed() {
        <span class="hljs-keyword">this</span>.$contact(<span class="hljs-string">'u-code-example'</span>, (codeExampleVM) =&gt; {
            codeExampleVM.$el.style.boxShadow = <span class="hljs-string">''</span>;
        });
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>API</h2>
<h3>Methods</h3>
<h4>$contact(condition, callback)</h4>
<p>联系符合条件的上级组件。</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>condition</td>
<td>String, Function&lt;$parent, Boolean&gt;</td>
<td>查找条件。如果为字符串，则根据上级组件的<code>name</code>是否与之相同来判断；如果为函数，传入上级组件实例，根据返回的布尔值判断。</td>
</tr>
<tr>
<td>callback</td>
<td>Function&lt;$parent&gt;</td>
<td>联系成功时的回调函数。</td>
</tr>
</tbody>
</table>
<h4>$dispatch(condition, eventName, ...args)</h4>
<p>向符合条件的上级组件发送事件。</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>condition</td>
<td>String, Function&lt;$parent, Boolean&gt;</td>
<td>查找条件。如果为字符串，则根据上级组件的<code>name</code>是否与之相同来判断；如果为函数，传入上级组件实例，根据返回的布尔值判断。</td>
</tr>
<tr>
<td>eventName</td>
<td>String</td>
<td>发送的事件名称。</td>
</tr>
<tr>
<td>...args</td>
<td>Array&lt;Any&gt;</td>
<td>发送的事件参数。</td>
</tr>
</tbody>
</table>
<h4>$broadcast(condition, eventName, ...args)</h4>
<p>向下级组件广播事件。</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>condition</td>
<td>String, Function&lt;$parent, Boolean&gt;</td>
<td>查找条件。如果为字符串，则根据上级组件的<code>name</code>是否与之相同来判断；如果为函数，传入上级组件实例，根据返回的布尔值判断。</td>
</tr>
<tr>
<td>eventName</td>
<td>String</td>
<td>发送的事件名称。</td>
</tr>
<tr>
<td>...args</td>
<td>Array&lt;Any&gt;</td>
<td>发送的事件参数。</td>
</tr>
</tbody>
</table>
<h4>$emitPrevent(name, $event, senderVM, ...args)</h4>
<p>抛出一个包含<code>preventDefault</code>方法的事件。</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>String</td>
<td>事件名称</td>
</tr>
<tr>
<td>$event</td>
<td>Object</td>
<td>事件对象</td>
</tr>
<tr>
<td>senderVM</td>
<td>Vue</td>
<td>发送事件的组件实例</td>
</tr>
<tr>
<td>...args</td>
<td>Array&lt;Any&gt;</td>
<td>其他参数</td>
</tr>
</tbody>
</table>
</u-article></template>

<script>
    import Component0 from "./anondemo-35aef168-476.vue";

    export default {
        components: {
'anondemo-35aef168-476': Component0,
},
    }
</script>
