module.exports = "<template><u-article><h1>MPubSub 发布订阅模式</h1>
<p>在 Vue 原生的功能中，不管是<code>$emit</code>和<code>$on</code>，还是<code>provide</code>和<code>inject</code>，在组件间传递变量都有些局限，特别是在两个不相关的组件之间。</p>
<p>本组件包含了两个 Mixins：MPublisher 和 MSubscriber，用于在任意两个组件之间通过发布订阅模式传递数据。</p>
<p>在 Mixin MPublisher 时，选项<code>publish</code>为一个对象，键为需要发布的主题，值为需要监听的表达式或函数。在 Mixin MSubscriber 时，选项<code>subscribe</code>为一个对象，键为需要订阅的主题，值为订阅时执行的方法。</p>
<blockquote>
<p>注意：这对 Mixins 和普通的发布订阅模式有一点区别是，订阅时会执行上一次发布的结果，方便一些异步订阅的场景。</p>
</blockquote>
<h2>示例</h2>
<p>下面的例子中展示了，如何将一个实例中的数据或状态，通过发布订阅者模式，传递到另一个实例中。</p>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="proto-ui.vusion/src/components/m-pub-sub.vue/README.md">
    <div><anondemo-a43b1396-408 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"onClick"</span>&gt;</span>发布<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { MPublisher } <span class="hljs-keyword">from</span> <span class="hljs-string">'@@'</span>;

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">mixins</span>: [MPublisher],
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value</span>: <span class="hljs-literal">undefined</span>,
        };
    },
    <span class="hljs-attr">publish</span>: {
        <span class="hljs-attr">someTopic</span>: <span class="hljs-string">'value'</span>,
    },
    <span class="hljs-attr">methods</span>: {
        onClick() {
            <span class="hljs-keyword">this</span>.value = <span class="hljs-keyword">this</span>.value === <span class="hljs-string">'red'</span> ? <span class="hljs-string">'blue'</span> : <span class="hljs-string">'red'</span>;
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="proto-ui.vusion/src/components/m-pub-sub.vue/README.md">
    <div><anondemo-dd25bef0-462 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">:class</span>=<span class="hljs-string">"$style.root"</span> <span class="hljs-attr">:style</span>=<span class="hljs-string">"{ background: received }"</span>&gt;</span>{{ String(received) }}<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { MSubscriber } <span class="hljs-keyword">from</span> <span class="hljs-string">'@@'</span>;

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">mixins</span>: [MSubscriber],
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">received</span>: <span class="hljs-literal">undefined</span>,
        };
    },
    <span class="hljs-attr">subscribe</span>: {
        someTopic(value) {
            <span class="hljs-keyword">this</span>.received = value;
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">module</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.root</span> {
    <span class="hljs-attribute">color</span>: white;
    <span class="hljs-attribute">padding</span>: <span class="hljs-number">10px</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>MPublisher API</h2>
<h3>Options</h3>
<table>
<thead>
<tr>
<th>Option</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>publish</td>
<td>{ [prop: String]: String | Function | Object }</td>
<td></td>
<td>键为需要发布的主题，值为需要监听的表达式或函数。值也可以为一个对象<code>{ expOrFn: String \| Function, deep: Boolean, immediate: Boolean }</code>，<code>deep</code>和<code>immediate</code>与官方<code>watch</code>中的意义相同。</td>
</tr>
</tbody>
</table>
<h2>MSubscriber API</h2>
<h3>Options</h3>
<table>
<thead>
<tr>
<th>Option</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>subscribe</td>
<td>{ [prop: String]: String | Function | Object }</td>
<td></td>
<td>键为需要订阅的主题，值为订阅时执行的方法。值也可以为一个对象<code>{ handler: String \| Function, once: Boolean }</code>，<code>once</code>开启，则只订阅一次。</td>
</tr>
</tbody>
</table>
</u-article></template>

